        status: '试用验证',
        introduction: '随着无人机等航拍手段广泛应用，多源空照图数据作为战场信息的重要载体，其目标检测识别关联对于战场信息资源的开发利用意义重大。基于此本团队结合边缘计算和人工智能技术，提出了一套无人机载目标快速检测与识别软/硬件解决方案。在硬件方面，团队将算力强大的NVIDIA AGX Orin边缘端改造为机载计算机搭载在大疆M300RTK四旋翼上，加入图传模块和稳压供电模块成功搭建了一套单兵可拓展的无人机实时侦察系统。在软件方面，团队结合高性能的目标检测算法YOLOX和旋转框目标检测算法Oriented R-CNN、改进的DeepSORT跟踪算法和团队设计的单机无源视觉定位算法，基于ROS操作系统实现了无人机在飞行过程中利用可见光/红外灯多源图像信息对行人、汽车等十类目标的实时精确检测、跟踪、计数和定位，从而为指挥决策提供重要依据。',
